.search-form {
  justify-content: center;
  gap: 20px;
  margin: 50px 0 30px;
  display: flex;
}

input {
  width: 300px;
  height: 30px;
  border: 1px solid #000;
  border-radius: 15px;
  padding-left: 15px;
  font-size: 100%;
}

button[type="submit"] {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 15px;
}

.gallery {
  width: 1200px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  margin: 0 auto;
  padding-bottom: 40px;
  display: flex;
}

.photo-card {
  width: calc(25% - 18px);
  border-radius: 0 0 4px 4px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

img {
  max-width: 100%;
  height: 75%;
  object-fit: fill;
  display: block;
}

p {
  text-align: center;
}

.info {
  gap: 5px;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.load-more:disabled {
  background-color: #c1c1c1;
}

.load-more-hidden {
  display: none;
}

